@use './scss/index' as *;

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400&display=swap');

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

html {
   font-size: 62.5%;
   scroll-behavior: smooth;
}

body {
   font-family: 'Lato', sans-serif;
   font-size: 1.6rem;
}

a {
   text-decoration: none;
}

.section {
   max-width: $max-width;
   margin-left: auto;
   margin-right: auto;
   padding: 15px $main-padding;
}

.cardsContainer {
   display: flex;
   flex-direction: column;
   flex-wrap: wrap;
   align-items: center;
   margin: 20px 0;
   gap: $cards-gap-y 0;

   @include tablet {
      flex-direction: row;
      gap: $cards-gap-y $cards-gap-x;
   }

   @include desktop {
      margin: 30px 0;
   }
}

.loaderSpinnerContainer {
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 50px 0;
}

.loaderSpinnerBigContainer {
   height: calc(100vh - $nav-height);
   display: flex;
   align-items: center;
   justify-content: center;
}

.authForm,
.joinForm,
.addEventForm {
   max-width: 500px;
   width: 100%;
   padding: 30px 15px;

   @include phone {
      padding: 30px 25px;
   }

   @include tablet {
      padding: 30px 35px;
   }

   .authHeader,
   .joinHeader,
   .addEventHeader {
      font-size: 2.2rem;
      margin-bottom: 30px;
      color: $color-black;
   }

   .authLabel,
   .joinLabel,
   .addEventLabel {
      display: block;
      font-size: 1.4rem;
      margin-bottom: 10px;
      color: $color-black;

      @include phone {
         font-size: 1.6rem;
      }
   }

   .authInput,
   .joinInput,
   .addEventInput,
   .addEventTextarea,
   .addEventSelect,
   .addEventDateTime {
      display: block;
      width: 100%;
      margin-bottom: 15px;
      padding: 10px 15px;
      border: 1px solid $color-gray;
      border-radius: $border-radius;
      outline: none;
      font-size: 1.5rem;
      color: lighten($color-black, 10%);

      @include phone {
         font-size: 1.7rem;
      }
   }

   .authAlreadyContainer {
      display: flex;
      align-items: center;
      margin-bottom: 25px;
   }

   .authAlreadyText {
      margin-right: 5px;
      font-size: 1.4rem;
      color: $color-black;

      @include phone {
         font-size: 1.6rem;
      }
   }

   .authAlreadyButton {
      font-size: 1.4rem;
      color: $color-purple;

      @include phone {
         font-size: 1.6rem;
      }
   }

   .authSubmitButton,
   .joinSubmitButton,
   .addEventSubmitButton {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 52.5px;
      padding: 15px 20px;
      background-color: $color-purple;
      color: $color-white;
      border: none;
      border-radius: $border-radius;
      font-size: 1.8rem;
      cursor: pointer;

      @include phone {
         font-size: 2rem;
      }
   }

   .authErrorContainer,
   .joinErrorContainer,
   .addEventErrorContainer {
      position: relative;
      margin-bottom: 25px;
      padding-left: 15px;
   }

   .authErrorContainer::after,
   .joinErrorContainer::after,
   .addEventErrorContainer::after {
      content: '';
      position: absolute;
      width: 4px;
      top: -2px;
      bottom: -2px;
      left: 0;
      background-color: $color-red;
      border-radius: $border-radius;
   }

   .authErrorText,
   .joinErrorText,
   .addEventErrorText {
      color: $color-red;
      font-weight: 700;
   }
}
